// 
let VOM_c = gUndefined;
function VOM_cover() {
    if (VOM_c === gUndefined) {
        VOM_c = $("#write>pre.md-meta-block:first-child + h6, #write>h6:first-child");
        if (VOM_c.length === 0) {
            VOM_c = gUndefined;
            if (VOM_docTitle() === gUndefined)
                WARN(s_Failed + "VOM.c ], maybe no cover: " + WINDOW_getHref());
        }
    }
    return VOM_c;
}

// 
let VOM_bc = gUndefined;
function VOM_backcover() {
    //  #vk-footer-area 
    if (VOM_bc === gUndefined) {
        let fns = $("." + s_FootnotesArea);
        if (fns !== gUndefined) {
            let backcover = fns.prev(),
                tagName = V_util_getTagName(backcover);
            if (tagName === "h1")
                VOM_bc = backcover;
        }
        else {
            VOM_bc = gUndefined;
            WARN(s_Failed + "VOM.bc ], maybe no backcover");
        }
    }
    return VOM_bc;
}

