    // 
    core : function () {
        const u = navigator.userAgent;
        return {
            trident: u.i("Trident") > -1, // IE
            presto: u.i("Presto") > -1, // Opera
            webkit: u.i("AppleWebKit") > -1, // 
            gecko: u.i("Gecko") > -1 && u.i("KHTML") === -1 // 
        };
    }(),

    // 
    device : function () {
        const u = navigator.userAgent;
        return {
            mobile: u.i("Mobile") > -1, // 
            iOS: u.i("iPhone") > -1, // iOS 
            android: u.i("Android") > -1 || u.i("Linux") > -1, // android  UC 
            iPhone: u.i("iPhone") > -1, //  iPhone  QQHD 
            iPad: u.i("iPad") > -1 //  iPad
        };
    }(),

