    browser : function () {
        const u = navigator.userAgent;
        return {
            Chrome: (u.i("Chrome") > -1 && u.i("Edg") < 0) || u.i("CriOS") > -1, // Chrome 
            Firefox: u.i("Firefox") > -1 || u.i("FxiOS") > -1, // Firefox 
            Safari: u.i("Safari") > -1 && (u.i("Chrome") < 0 || u.i("CriOSc") < 0 || u.i("Edg") < 0 || u.i("FxiOS") < 0), // Safari 
            Edge: u.i("Edg") > -1 // Edge 
        };
    }(),

    // 
    browserVer : function () {
        const u = navigator.userAgent;
        return {
            Chrome: u.m(/Chrome\/[\d.]+/gi) ? u.m(/Chrome\/[\d.]+/gi)[0].m(/\d+/)[0] : "0", // Chrome 
            Firefox: u.m(/Firefox\/[\d.]+/gi) ? u.m(/Firefox\/[\d.]+/gi)[0].m(/\d+/)[0] : "0", // Firefox 
            Safari: u.m(/Version\/[\d.]+.+Safari\/[\d.]+/gi) ? u.m(/Version\/[\d.]+.+Safari\/[\d.]+/gi)[0].m(/\d+/)[0] : "0", // Safari 
            Edge: u.m(/Edg\/[\d.]+/gi) ? u.m(/Edg\/[\d.]+/gi)[0].m(/\d+/)[0] : "0" // Edge 
        };
    }(),

    // 
    os : function () {
        const u = navigator.userAgent;
        return {
            Windows: /windows|win32|win64/i.test(u), //  Windows 
            macOS: /macintosh/i.test(u), //  macOS 
            iOS: /iphone os/i.test(u), //  iOS 
            Linux: /linux/i.test(u) //  Linux 
        };
    }(),

    // 
    language : function () {
        const lang = navigator.language;
        return {
            full: lang.l(),
            base: lang.s(0, 2),
            subset: lang.s(3, lang.length)
        };
    }(),

