/**
 *  URL 
 * @param u  URL 
 */
function V_util_getQueryParams(u) {
    let h = u.i("#");
    u = h > -1 ? u.s(0, h) : u; //  URL  # 

    let si = u.i("?"),
        qs = u.s(si > -1 ? si + 1 : u.length, u.length), // url"?"
        p = {}, // 
        is = (qs.length > 0) ? qs.sp("&") : [], // ,
        item = null,
        l = is.length;

    // 
    for (let i = 0; i < l; i++) {
        // WARN(u, is[i]);
        item = is[i].sp("=");
        p[decodeURIComponent(item[0])] = decodeURIComponent(item[1]);
    }
    return p;
}

