/**
 * 
 * @param str 
 * @returns string 
 */
function V_formatting_currency(str) {
    //  $  Markdown  \$
    // HK$
    str = str.r(/<\/span><span>([$])<\/span><span>/, "$1");
    str = str.r(/>([$])<\/span><span>/, ">$1");
    //  html()  >  > 
    return str.r(/>([a-z$]{0,3})\s/i, '><span class="v-tbl-col-fmt-currency">$1</span>');
}
