/**
 * 
 * @param tips 
 */
function ParagraphNav(tips) {
    let T = this;
    T.tips = tips;
    T.count = 0; // 
    T.curIdx = -1; // 
    T.enabled = gFalse; // 

    T.toolbar = gUndefined; // 

    /**
     * 
     */
    T.cur = function () {
        if (T.curIdx === -1)
            return gUndefined;
        return $("[" + s_DataId + "='vk-pg-" + T.curIdx + "']");
    }

    /**
     * 
     * @returns boolean truefalse
     */
    T.tg = function (target) {
        T.enabled = !T.enabled;
        if (T.enabled) {
            JQ_addClass(T.toolbar.btns[s_ParagraphNav], s_Selected);
            MoreDocContent_hideAfter();

            // 
            let key1 = V_ui_wrap_kbd("J") + " / " + V_ui_wrap_kbd("K") + tag_2space,
                key2 = tag_2space + tag_2space + V_ui_wrap_kbd("H") + " / " + V_ui_wrap_kbd("L") + tag_2space,
                key3 = tag_2space + tag_2space +"-" + tag_2space + tag_2space + V_ui_wrap_kbd("ESC") + tag_2space;
            T.tips.show([
                key1 + "/" + key2 + "/" + key3 + "",
                key1 + "front/back paragraph" + key2 + "front/back ten paragraphs" + key3 + "Exit"
            ][V_lang_id]);

            T.goto(target);
            return gTrue;
        }
        else {
            T.hide();
        }
        return gFalse;
    }

    /**
     * 
     */
    T.add = function (item) {
        item.a(s_DataId, "vk-pg-" + T.count);
        item.a(s_DataPgIdx, T.count);
        T.count++;

        // 
        item.uC().ck(function () {
            // 
            if (!iParagraphNav.enabled) {
                if (ThreeClicker_tick()
                    && iParagraphNav.tg(item)) {
                        iSpotlight.hide();
                        iLaserPointer.hide();
                    }
            }
            else {
                T.goto(item);
                if (ThreeClicker_tick()) {
                    iParagraphNav.tg(item);
                }
            }
        });
    }

    /**
     * 
     * @param step 
     * @returns boolean truefalse
     */
    T.prev = function (step) {
        if (!T.enabled)
            return gFalse;

        T.blurFocus();

        // 
        if (T.curIdx > 0) {
            T.curIdx = T.curIdx - step;

            if (T.curIdx < 0)
                T.curIdx = 0;

            //  item 
            if (!T.goto(0))
                T.prev(1);

            return gTrue;
        }
        return gFalse
    }

    /**
     * 
     * @param step 
     * @returns boolean truefalse
     */
    T.next = function (step) {
        if (!T.enabled)
            return gFalse;

        T.blurFocus();

        // 
        if (T.curIdx < T.count - 1) {
            T.curIdx = T.curIdx + step;

            //  step 
            if (T.curIdx > T.count - 1)
                T.curIdx = T.count - 1;

            // item
            if (!T.goto(0))
                T.next(1);

            return gTrue;
        }
        return gFalse;
    }

    /**
     * _blockFocusItemIndex
     * @param target 0: /0: 
     */
    T.goto = function (target) {
        T.blurFocus();

        // 
        let item = (target === 0)
            ? T.cur()
            : target;

        // 
        // 
        if (item === gUndefined || item.isHidden() || item.o() === gUndefined)
            return gFalse;

        // 
        JQ_addClass(item, s_CssPgCurrentItem);
        T.curIdx = JS_parseInt(item.a(s_DataPgIdx));

        // 
        let height = item.ht() * 3;
        if (item.oT() !== 0
            && ((item.oT() - height) < $(document).scrollTop()
            || (item.oT() + height) > ($(document).scrollTop() + $(window).ht()))) {
                DOM_html().scrollTop(item.oT() - $(window).ht() / 2);
        }

        return gTrue; // 
    }

    /**
     * 
     */
    T.blurFocus = function () {
        if (T.cur() !== gUndefined)
            JQ_removeClass(T.cur(), s_CssPgCurrentItem);
    }

    /**
     * 
     */
    T.hide = function () {
        MoreDocContent_refresh();
        T.tips.hide();
        JQ_removeClass(T.toolbar.btns[s_ParagraphNav], s_Selected);
        T.enabled = gFalse;
        T.blurFocus();
    }

    /**
     * 
     * @param code 
     * @param combKeys  Ctrl / Shift / Alt / Win / Cmd
     * @param event 
     */
    T.disposeHotkey = function (code, combKeys, event) {
        if (!T.enabled)
            return;

        let handled = false;
        switch (code) {
            case 74: // J
                TableCross_hide();
                if (T.next(1)) // 1
                    ExtQuote_autoUnfold(); // 
                handled = true;
                break;
            case 75: // K
                TableCross_hide();
                if (T.prev(1)) // 1
                    ExtQuote_autoUnfold(); // 
                handled = true;
                break;
            case 72: // H
                TableCross_hide();
                if (T.prev(10)) // 10
                    ExtQuote_autoUnfold(); // 
                handled = true;
                break;
            case 76: // L
                TableCross_hide();
                if (T.next(10)) // 10
                    ExtQuote_autoUnfold(); // 
                handled = true;
                break;
            case 27: // ESC
                if (T.enabled) {
                    T.hide();
                    handled = true;
                }
                break;
        }

        // 
        if (handled)
            event.preventDefault();
    }
}

/**
 * 
 */
ParagraphNav.init = function () {
    iParagraphNav = new ParagraphNav(new BottomTips(s_ParagraphNav));
    // 
    iParagraphNav.toolbar = iToolbar;

    // 
    $("li>p:only-child").contents().unwrap();

    // 
    $("h1, h2, h3, h4, h5, h6, ul>li, ol>li, p[class!=md-toc-content], figure, .md-diagram-panel, .MathJax_SVG_Display").e(function () {
        let item = $(this);
        //  p li > p
        if (item.is(":visible") && item.ch("p").length === 0) {
            iParagraphNav.add(item);

            // 
            item.on("dblclick", function () {
                // 
                //  ThreeClicker_tick() 
                ThreeClicker_active();
            });
        } // if
    });
}

