/**
 * 
 */
function LinkTool_checkLink() {
    if (!LinkTool_enabled)
        return;

    let count = 0;
    // 
    $("#write a").e(function () {
        let _t = $(this),
            href = _t.a(s_Href);
        // 
        //  href="#"  Typora 1.6.x  Typora  HTML  # 
        if (href === gUndefined) // || href.length <= 1)
            return gTrue;

        // --------------------
        // 
        //  #mjx-  jQuery 
        if (href === "#")
            __addErrorLink(_t);
        else if (href.sW("#") && !href.sW("#mjx-")) {
            // 
            let anchor = href.s(1, href.length);
            // 
            if ($("#write #" + anchor + ", #write a[name='" + anchor + "']").length === 0) {
                __addErrorLink(_t);
            }
        }
    });

    // 
    if (count === 0) {
        JQ_remove(LinkTool_ui);
    }
    else {
        // 
        JQ_addClass(LinkTool_ui, s_Error);
        LinkTool_ui.h(LinkTool_icon_error);

        // 
        LinkTool_ui.uC().ck(function () {
            if (LinkTool_panel_list.isHidden())
                LinkTool_show();
            else
                LinkTool_hide();
        });
    }

    // 
    function __addErrorLink(aObj) {
        count++;

        //  id 
        let id = "vk-error-anchor" + count;
        aObj.a(s_Id, id);

        LinkTool_addToCheck(id, " <strong>" + [
            "",
            "Invalid Inner Link"
        ][V_lang_id] + ":</strong> " + aObj.t());
    }
}

