/**
 * 
 * @param mask 
 */
function InfoTips(mask) {
    let T = this;
    T.ui = $(".v-info-tips");
    T.aniTimer = null;

    // 
    T.ui.uC().ck = function () {
        T.hide();
    }

    // 
    T.mask = mask;
    T.mask.bindPartner(this, T.ui);

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.inform = function (message, delay, mask, target) {
        T.show(message, delay, mask, "inform", target);
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.bubble = function (message, delay, mask, target) {
        T.show(message, delay, mask, s_Bubble, target);
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param target 
     */
    T.error = function (message, delay, mask, target) {
        T.show(message, delay, mask, s_Error, target);
    }

    /**
     * 
     * @returns boolean
     */
    T.isShowed = function () {
        return T.ui.c(s_Display) !== s_None;
    }

    /**
     * 
     * @param message 
     * @param delay  ms 
     * @param mask 
     * @param type info/error
     * @param target 
     */
    T.show = function (message, delay, mask, type, target) {
        clearTimeout(T.aniTimer);
        T.aniTimer = null;

        T.ui.h(message);

        // 
        JQ_removeClass(T.ui, s_Error);
        JQ_removeClass(T.ui, s_Bubble);
        T.ui.c(s_Width, "")
            .c(s_Height, "")
            .c(s_Right, s_Auto)
            .c(s_Bottom, s_Auto)
            .c(s_BorderRadius, s_varRB);
        if (type === s_Error)
            JQ_addClass(T.ui, s_Error);
        else if (type === s_Bubble)
            JQ_addClass(T.ui, s_Bubble);

        // 
        if (target !== gUndefined) {
            V_ui_moveToTarget(T.ui, target);
        }
        // 
        else
            V_ui_moveToCenter(T.ui);

        // V_ui_show(T.ui);
        T.ui.show();

        // 
        if (delay != null) {
            T.aniTimer = setTimeout(function () {
                    T.hide();
                }, delay);
        }

        // 
        if (mask)
            T.mask.show();
    }

    /**
     * 
     */
    T.hide = function () {
        T.ui.hide();
        T.mask.hide();
    }

    /**
     * 
     * @param code 
     * @param combKeys  Ctrl / Shift / Alt / Win / Cmd
     * @param event 
     */
    T.disposeHotkey = function (code, combKeys, event) {
        if (T.ui.isHidden())
            return;

        let handled = false;
        switch (code) {
            case 27: // ESC
                if (T.isShowed()) {
                    T.hide();
                    handled = true;
                }
                break;
        }

        // 
        if (handled)
            event.preventDefault();
    }
}

