    else
        td.pp("<label class='v-tbl-row-g-btn'>" + RowGroup_icoClosed + "</label>");

    // 
    JQ_removeClass(td, s_CssTableRowGroupNotFolder);
    JQ_addClass(td, "v-tbl-row-g-folder");
    JQ_addClass(tdHadIdent, "v-tbl-row-g-identer-folder");

