            //  mini 
            audio.after("<div id='vk-id-mini-audio" + V_doc_counter_audiomini + "-control"
                + "' " + s_DataTitle + "='mini audio " + V_doc_counter_audiomini
                + "' class='v-audio-mini-control'></div>");

