                    imgH = _t.ht() + padding * 2;
                card.c(s_Top, borderBottomWidth + _t.ps().top + imgH - h);

                //  left 
                // let navOffset = iNavCenter.showed
                //     ? s_writeMarginLeft
                //     : "0px";
                card.c(s_Left, _t.ps().left);

